export interface Edges {
    top: boolean;
    left: boolean;
    right: boolean;
    bottom: boolean;
}

const isEdge = (pos: number, size: number): Edges => {
    return {
        top: pos < size,
        left: pos % size === 0,
        right: (pos + 1) % size === 0,
        bottom: pos >= size * (size - 1),
    };
};

export const getPivotOptions = (pos: number, size: number): any[] => {
    const { left, right, top, bottom } = isEdge(pos, size);
    //returns number or false
    return [
        !left && pos - 1,
        !right && pos + 1,
        !bottom && pos + size,
        !top && pos - size,
    ].filter((x) => typeof x === "number");
};
