import styled from "styled-components";
import { FunctionalComponent, h } from "preact";
import { INITIAL_COLOR } from "../constants/color";
import { Connect } from "redux-zero/preact";
import actions from "./actions";
import { getPivotOptions } from "../helpers/edge";
import { getRandomElement } from "../helpers/random";

const Button = styled.button`
    color: ${(props) => props.color};
    font-size: 2em;
    font-family: Arial, serif;
    font-weight: bolder;
    width: 60px;
    height: 60px;
    border: ${(props) => (props.color !== INITIAL_COLOR ? "3px" : "2px")} solid
        ${(props) => props.color};
    border-radius: 3px;
`;

export interface ButtonProps {
    value?: string;
    color: string;
    position: number;
    size: number;
}

export const Tile: FunctionalComponent<ButtonProps> = ({
    value = "",
    color,
    position,
    size,
}): JSX.Element => {
    return (
        <Connect actions={actions}>
            {({ path, next }: any) => {
                const imNext = position === path[path.length - 1];
                if (imNext) {
                    const pivotOptions = getPivotOptions(position, size);
                    const availableOptions = pivotOptions.filter(
                        (option: number) => !path.includes(option)
                    );
                    next(getRandomElement(availableOptions));
                }
                return (
                    <Button
                        color={color}
                        key={value}
                        onClick={() => {
                            if (path.length === 0) {
                                next(position);
                            }
                        }}
                    >
                        {value}
                    </Button>
                );
            }}
        </Connect>
    );
};
export default Tile;
