import { FunctionalComponent, h } from "preact";
import styled from "styled-components";
import { getEmptyTiles } from "./helpers/empty-tiles";
import { Connect } from "redux-zero/preact";
import Tile from "./elements/tile";
import { getColor } from "./helpers/color";
import { getChar } from "./helpers/char";
import { State } from "../../store/store";

interface Props {
    size: number;
    message: string;
}

const Grid = styled.div`
    display: grid;
    grid-template-columns: repeat(
        ${(props: { size: number }): number => props.size},
        60px
    );
    gap: 5px 5px;
`;

const Walker: FunctionalComponent<Props> = ({ size, message }) => {
    const tiles = getEmptyTiles(size);

    return (
        <Connect>
            {({ path }: State) => (
                <div>
                    <Grid size={size}>
                        {tiles.map((tile, index) => {
                            if (
                                !path ||
                                !path.includes(index) ||
                                path.indexOf(index) >= message.length
                            ) return tile;

                            return (
                                <Tile
                                    key={index}
                                    position={index}
                                    size={size}
                                    color={getColor(
                                        message,
                                        path.indexOf(index)
                                    )}
                                    value={getChar(
                                        message,
                                        path.indexOf(index)
                                    )}
                                />
                            );
                        })}
                    </Grid>
                </div>
            )}
        </Connect>
    );
};

export default Walker;
