import { INITIAL_COLOR } from "../constants/color";
import Tile from "../elements/tile";
import { h } from "preact";

export const getEmptyTiles = (size: number): JSX.Element[] => {
    return Array(size * size)
        .fill(null)
        .map((unused, index) => (
            <Tile color={INITIAL_COLOR} position={index} size={size} />
        ));
};
